@charset 'UTF-8';

// Default Variables

@slick-loader-path: './';
@slick-arrow-color: white;
@slick-dot-color: black;
@slick-dot-color-active: @slick-dot-color;
@slick-prev-character: '←';
@slick-next-character: '→';
@slick-dot-character: '•';
@slick-dot-size: 6px;
@slick-opacity-default: .75;
@slick-opacity-on-hover: 1;
@slick-opacity-not-active: .25;

/* Arrows */

.slick-prev ,
.slick-next {
    font-size: 0px;
    line-height: 0px;
    position: absolute;
    display: block;
    padding: 0;
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background-color: transparent;
    &.slick-disabled:before {
        opacity: @slick-opacity-not-active;
    }
}

.slick-prev {
    left: -25px;
    &[dir='rtl'] {
        right: -25px;
        left: auto;
    }
    &:before {
        content: @slick-prev-character;
        &[dir='rtl'] {
            content: @slick-next-character;
        }
    }
}

.slick-next {
    right: -25px;
    &[dir='rtl'] {
        right: auto;
        left: -25px;
    }
    &:before {
        content: @slick-next-character;
        &[dir='rtl'] {
            content: @slick-prev-character;
        }
    }
}

/* Dots */

.slick-dotted .slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -25px;
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: center;
    li {
        position: relative;
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            font-size: 0px;
            line-height: 0px;
            display: block;
            width: 20px;
            height: 20px;
            padding: 5px;
            cursor: pointer;
            color: transparent;
            border: 0;
            outline: none;
            background: transparent;
            &:hover ,
            &:focus {
                outline: none;
                &:before {
                    opacity: @slick-opacity-on-hover;
                }
            }
            &:before {
                font-size: @slick-dot-size;
                line-height: 20px;
                position: absolute;
                top: 0;
                left: 0;
                width: 20px;
                height: 20px;
                content: @slick-dot-character;
                text-align: center;
                opacity: @slick-opacity-not-active;
                color: @slick-dot-color;

                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button:before {
            opacity: @slick-opacity-default;
            color: @slick-dot-color-active;
        }
    }
}
